import * as lottie from "lottie-miniprogram";

Component({
    properties: {
        loop: {
            type: Boolean,
            value: true
        },
        auto: {
            type: Boolean,
            value: true
        },
        data: Object,
        uri: String
    },
    observers: {
        data() {
            this.loadAnimation();
        },
        uri() {
            this.loadAnimation();
        }
    },
    methods: {
        loadAnimation() {
            const data = this.data.data;
            const uri = this.data.uri;
            if (data === null && uri === "") {
                return;
            }
            if (this.ani !== undefined) {
                // TODO: this.ani.destroy(); 方法报错，待官方修复
                this.ani.stop();
                this.ani = undefined;
            }
            wx.createSelectorQuery()
                .in(this)
                .select("#canvas")
                .node(res => {
                    const canvas = res.node;
                    const context = canvas.getContext("2d");

                    // 临时解决 Lottie-miniprogram 动画模糊的问题
                    canvas.width *= 2;
                    canvas.height *= 2;

                    lottie.setup(canvas);
                    const option: lottie.LoadAnimationOption = {
                        loop: this.data.loop,
                        autoplay: this.data.auto,
                        rendererSettings: {
                            context
                        }
                    };
                    if (data !== null) {
                        option.animationData = data;
                    }
                    if (uri !== "") {
                        option.path = uri;
                    }
                    this.ani = lottie.loadAnimation(option);
                })
                .exec();
        }
    }
});
